@import url(https://fonts.googleapis.com/css2?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap);
@font-face {
  font-family: 'icons';
  src:  url(/joeowen.ca/static/media/icomoon.14ee58c5.eot);
  src:  url(/joeowen.ca/static/media/icomoon.14ee58c5.eot#iefix) format('embedded-opentype'),
    url(/joeowen.ca/static/media/icomoon.48f49e4b.ttf) format('truetype'),
    url(/joeowen.ca/static/media/icomoon.950ab3ba.woff) format('woff'),
    url(/joeowen.ca/static/media/icomoon.d3960a72.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #1a1a1a;
  color: white;
}

#root {
  width: 100%;
}

h1, h2, h3 {
  font-family: 'Lora', serif;
  font-weight: 400;
}

p {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 11pt;
}

a {
  color: white;
  text-decoration: none;
}

.active {
  text-decoration: underline;
}

.header {
  position: fixed;
  box-sizing: border-box;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 80px;
  font-family: 'Lora', serif;
}

.header.background {
  background-image: linear-gradient(#1A1A1A, transparent);
}

.header-img {
  position: absolute;
  top: 10px;
  left: 10px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 100px;
  transition: left 0.5s;
}

.header-text {
  position: absolute;
  top: 24px;
  left: 70px;
  display: inline-block;
  vertical-align: middle;
  font-weight: 400;
  transition: left 0.5s;
}

.header-img.hidden {
  left: -70px;
}

.header-text.hidden {
  left: 13px;
}

.header span {
  font-family: 'Source Sans Pro', sans-serif;
}

.intro {
  z-index: -1;
}

.content {
  padding: 80px 10px 10px 10px;
}

.content h1 {
  text-align: center;
  font-size: 18pt;
  padding-bottom: 20px;
}
@media only screen and (max-width: 600px) {
    .header {
        padding: 10px;
    }
    
    .header-img {
        width: 60px;
    }
    
    .header h1 {
        font-size: 18pt;
        padding-left: 10px;
        color: white;
    }

    #header-text {
        color: black;
    }
    
    .header span {
        font-size: 12pt;
        padding-left: 5px;
    }
}
@media only screen and (max-width: 600px) {
    .showcase {
        padding: 100vh 10px 10px 10px;
    }

    .showcase h1 {
        font-size: 18pt;
        text-align: center;
        padding-bottom: 24px;
    }
    
    .photography-showcase {
        margin-bottom: 20px;
    }
    
    .photography-showcase img {
        display: block;
        width: 60%;
        margin: auto;
        padding-bottom: 10px;
    }

    .photography-showcase h2 {
        font-size: 16pt;
        padding-bottom: 10px;
    }
    
    .videos-showcase img {
        display: block;
        width: 90%;
        margin: auto;
        padding-bottom: 10px;
    }
    
    .videos-showcase h2 {
        font-size: 16pt;
        padding-bottom: 10px;
    }
    
}
@media only screen and (max-width: 600px) {
    .intro {
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background: url(/joeowen.ca/static/media/background-portrait-3.2580325b.jpg) no-repeat center center fixed;
        background-attachment: scroll;
        background-size: cover;
        top: 0px;
        left: 0px;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        margin-bottom: 20px;
        padding: 10px;
    }

    .intro-home-img {
        display: block;
        width: 120px;
        border-radius: 80px;
    }

    .intro-photography-img {
        width: 60%;
    }

    .intro h1 {
        margin: 10px 0px 15px 0px;
        font-size: 18pt;
        color: black;
    }

    .intro-text {
        font-weight: 100;
        margin: 0px 0px 10px 0px;
        padding: 7px 10px 10px 10px;
        background-color: rgba(0, 0, 0, 0.75);
    }

    .scroll-to-view {
        position: absolute;
        bottom: 30px;
    }
}
@media only screen and (max-width: 600px) {
    .nav-menu {
        position: fixed;
        box-sizing: border-box;
        z-index: 1;
        top: 0px;
        left: 0px;
        width: 100vw;
        height: 100vh;
        padding: 10px;
        background-color: rgba(0, 36, 64, 0.95);
        /*background-color: rgba(20, 32, 24, 0.95);*/
        -webkit-animation-name: fade;
        -webkit-animation-duration: 0.3s;
        animation-name: fade;
        animation-duration: 0.3s;
    }

    @-webkit-keyframes fade {
        from {-webkit-opacity: 0} 
        to {-webkit-opacity: 1}
    }

    @keyframes fade {
        from {opacity: 0} 
        to {opacity: 1}
    }

    .nav-options {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .social-links {
        font-family: 'icons' !important;
        font-size: 14pt;
        padding-top: 30px;
        color: white;
    }

    .social-links div {
        display: inline-block;
        width: 20px;
        padding: 10px;
    }

    .nav-options h2 {
        font-family: 'Source Sans Pro', sans-serif;
        padding-bottom: 10px;
    }

    .dropdown-menu {
        position: fixed;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        top: 10px;
        right: 10px;
    }

    .dropdown-menu.hidden {
        display: none;
    }

    #dropdown-open {
        color: black;
    }

    #dropdown-open::before {
        font-family: 'icons' !important;
        content: "\e901";
        font-size: 24pt;
    }

    #dropdown-close::before {
        font-family: 'icons' !important;
        content: "\e900";
        color: white;
        font-size: 24pt;
    }

    .instagram::before {
        content: "\ea92";
    }

    .youtube::before {
        content: "\ea9d";
    }

    .soundcloud::before {
        content: "\eac3";
    }

    .linkedin::before {
        content: "\eaca";
    }
}
@media only screen and (max-width: 600px) {
    .footer {
        background-color: #1a1a1a;
        padding: 10px;
    }

    .footer .contact {
        padding: 40px;
    }

    .footer p {
        text-align: center;
    }
}
.gallery {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

.column {
    width: 100%;
}

.thumbnail {
    display: block;
    margin-bottom: 10px;
    width: 100%;
}
.lightbox {
    display: flex;
    align-items: center;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.75);
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    z-index: 1000;
    overflow: auto;
    white-space: nowrap;
    -ms-scroll-snap-type: x mandatory;
        scroll-snap-type: x mandatory;
    scroll-padding: 0 0px;
    -webkit-animation-name: fade;
    -webkit-animation-duration: 0.3s;
    animation-name: fade;
    animation-duration: 0.3s;
}

.lightbox::-webkit-scrollbar {
    display: none;
}
  
.lightbox {
    -ms-overflow-style: none; 
    scrollbar-width: none; 
}

@-webkit-keyframes fade {
    from {-webkit-opacity: 0} 
    to {-webkit-opacity: 1}
}

@keyframes fade {
    from {opacity: 0} 
    to {opacity: 1}
}

.img-container {
    vertical-align: middle;
}

.lightbox img {
    display: inline-block;
    vertical-align: middle;
    margin: 0px 15% 0px 15%;
    max-width: 100%;
    max-height: 55%;
    scroll-snap-align: center;
    box-shadow: -2px 2px 20px black;
}
